.App {
  text-align: center;
}

.shape {
  background-color: cadetblue;
  border-radius: 15px;
  padding-bottom: 15px;
  padding-top: 15px;
}

.container {
  margin: auto;
  text-align: center;
}

.h2 {
  font-size: 50px;
  margin-top: 50px;
  font: oblique;
  margin-bottom: 50px;
  color: #0e0e0e;
}

.form {
  margin-left: 50px;
  margin-right: 50px;
  height: 68px;
}

.label {
  font-size: 18px;
  margin-bottom: 18px;
}

.input {
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 15px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
